import firebase from "firebase";
import _ from 'lodash'

const state = {
	fellow: {},
	fellows: [],
	loading: false,
  success: 0,
  failed: 0
}

const getters = {
  getFellow(state) {
    return state.fellow
  },
  getFellowById: (state, getters) => (id) => {
    return _.find(state.fellows, ['id', id])
  },
  getFellows(state) {
    return state.fellows
  },
  getSuccess(state) {
    return state.success
  },
  getFailed(state) {
    return state.failed
  }
}

const actions = {
  loadFellows({ commit, rootGetters, getters }) {
    firebase.database().ref("fellow").on("value", (snapshot) => {
      const fellow = []
      for(var a in snapshot.val()){
        var today = new Date()
        var birthDate = new Date(snapshot.val()[a].birthday)
        var age = today.getFullYear() - birthDate.getFullYear();
        var m = today.getMonth() - birthDate.getMonth();
        if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
          age = age - 1;
        }
        fellow.push({
          id: snapshot.val()[a].id,
          name: snapshot.val()[a].name,
          email: snapshot.val()[a].email,
          birthday: snapshot.val()[a].birthday,
          gender: snapshot.val()[a].gender,
          url: snapshot.val()[a].url,
          address: snapshot.val()[a].address,
          phone: snapshot.val()[a].phone,
          status: snapshot.val()[a].status,
          age: age
        })
      }
      commit('setFellows', fellow)
  	})
  },
  getFellowId({ commit, getters }, id) {
    const fellow = getters.getFellowById(id)
    commit('setFellow', fellow)
  },
  saveFellow({ commit, rootGetters }, data) {
    if (!data.photoProfile) {
      var key = firebase.database().ref().child('fellow').push().key;
      firebase.database().ref('/fellow/'+key).set({
        id: key,
        name: data.name,
        address: data.address,
        email: data.email,
        url: '',
        birthday: data.birthday,
        phone: data.phone,
        status: '1',
        gender: data.gender
        },(e)=>{
          if(e){
            commit('setFailed')
          }else{
            commit('setSuccess')
          }
      })
    } else {
      var fileName = data.url.name
      var storageRef = firebase.storage().ref('/profile/' + fileName)
      var uploadTask = storageRef.put(data.url)

      uploadTask.on('state_changed', function(snapshot) {

      }, function(error) {

      }, function() {
        uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
          var key = firebase.database().ref().child('fellow').push().key;
          firebase.database().ref('/fellow/'+key).set({
            id: key,
            name: data.name,
            address: data.address,
            email: data.email,
            url: downloadURL,
            birthday: data.birthday,
            phone: data.phone,
            status: '1',
            gender: data.gender
            },(e)=>{
              if(e){
                commit('setFailed')
              }else{
                commit('setSuccess')
              }
          })
        });
      })
    }
  },
  activeFellow({ commit }, data) {
    const postData = {
      name: data.name,
      id: data.id,
      address: data.address,
      email: data.email,
      birthday: data.birthday,
      url: data.url,
      phone: data.phone,
      gender: data.gender,
      status: data.status
    }
    firebase.database().ref("/fellow/"+data.id).set(postData,(err)=>{
      if(err){
        commit('setFailed')
      }else{
        commit('setSuccess')
      }
    })
  },
  updateFellow({ commit }, data) {
    if (!data.photoProfile) {
      firebase.database().ref("/fellow/"+data.id).set({
        id: data.id,
        name: data.name,
        address: data.address,
        email: data.email,
        birthday: data.birthday,
        phone: data.phone,
        url: '',
        gender: data.gender,
        status: data.status
        },(e)=>{
          if(e){
            commit('setFailed')
          }else{
            commit('setSuccess')
          }
      })
    } else {
      var fileName = data.url.name
      var storageRef = firebase.storage().ref('/profile/' + fileName)
      var uploadTask = storageRef.put(data.url)

      uploadTask.on('state_changed', function(snapshot) {

      }, function(error) {

      }, function() {
        uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
          const postData = {
            id: data.id,
            name: data.name,
            address: data.address,
            email: data.email,
            birthday: data.birthday,
            phone: data.phone,
            url: downloadURL,
            gender: data.gender,
            status: data.status
          }
          firebase.database().ref("/fellow/"+data.id).set(postData,(err)=>{
            if(err){
              commit('setFailed')
            }else{
              commit('setSuccess')
            }
          })  
        });
      })
    }
  },
  deleteFellow({ commit, dispatch }, data) {
    firebase.database().ref("fellow/"+data.id).remove().then(()=>{
        commit('setSuccess')
    }).catch((e)=>{
        commit('setFailed')
    })
  }
}

const mutations = {
	setFellows(state, fellows) {
    state.fellows = fellows
  },
  setFellow(state, data) {
    state.fellow = data
  },
  setSuccess(state) {
    state.success += 1
  },
  setFailed(state) {
    state.failed += 1
  },
}

export default {
  state,
  getters,
  actions,
  mutations
}